.bullet-area {
    display: block;
    border: solid 1px #ddd;
    resize: none;
    font-family: Times New Roman;
    font-size: 16px;
    min-width: 763px;
    width: 100%;
    box-sizing: border-box;
    overflow-x: auto;
    text-size-adjust: 0.5;
}

.bullet-tester {
    visibility: hidden;
    font-family: Times New Roman;
    font-size: 16px;
    border: solid 1px;
    position: absolute;
    height: auto;
    width: auto;
    white-space: pre;
}

#editor_title {
    padding: 8px 12px;
    border: 1px solid #ddd;
    font-weight: bold;
    font-size: 16px;
    background-color: #fafafa;
}

.guide {
    border: none;
    border-right: dashed 1px grey;
    position: absolute;
    top: 0px;
    height: 100%;
    width: 0px;
    box-sizing: border-box;
}

.guided-bullet-area {
    position: relative;
}

.toggle-label {
    font-family: Times New Roman;
    padding-right: 8px;
    height: 20px;
    display: inline-block;
    vertical-align: top;
}

.toggle-div {
    margin-bottom: 2px;
    float: right;
}

 /* The switch - the box around the slider */
.toggle {
  position: relative;
  display: inline-block;
  width: 32px;
  height: 20px;
}

/* Hide default HTML checkbox */
.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 12px;
  width: 12px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input {
    margin: 0px;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(12px);
  -ms-transform: translateX(12px);
  transform: translateX(12px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 20px;
}

.slider.round:before {
  border-radius: 50%;
} 
